import React, { Component } from 'react'
import { BrowserRouter } from 'react-router-dom'

import Routert from '../views/Router'

class App extends Component {
  render () {
    return (
      <div>
        <BrowserRouter>
          <Routert />
        </BrowserRouter>
      </div>
    )
  }
}

export default App



